import { Component, inject, signal, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { toSignal, toObservable } from '@angular/core/rxjs-interop';
import { of, catchError, tap, switchMap, forkJoin, map } from 'rxjs';

import { AppInstallationService } from '../../services/app-installation.service';
import { AppStoreService } from '../../services/app-store.service';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { AppDefinition } from '../../models/app-store.models';
import { isNullOrUndefined } from '../../../../shared/utils';

import { ButtonModule } from 'primeng/button';
import { CardModule } from 'primeng/card';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { TooltipModule } from 'primeng/tooltip';
import { AppCardComponent } from '../../components/app-card/app-card.component';

interface InstalledAppWithDetails extends AppDefinition {
  installationId: string;
  installedAt: string;
  isEnabled: boolean;
}

@Component({
  selector: 'app-my-installed-apps',
  standalone: true,
  imports: [
    CommonModule,
    RouterModule,
    ButtonModule,
    CardModule,
    ProgressSpinnerModule,
    TooltipModule,
    AppCardComponent,
  ],
  templateUrl: './my-installed-apps.component.html',
  styleUrls: ['./my-installed-apps.component.scss'],
})
export class MyInstalledAppsComponent {
  private readonly appInstallationService = inject(AppInstallationService);
  private readonly appStoreService = inject(AppStoreService);
  private readonly authDataService = inject(AuthDataService);

  // State Signals
  readonly isLoading = signal(true);
  readonly error = signal<string | null>(null);
  readonly currentUserId = this.authDataService.getUserProfile()?.id;

  // Refresh trigger
  private readonly refreshTrigger = signal(0);

  // Fetch installations and enrich with app details
  readonly installedApps = toSignal(
    toObservable(this.refreshTrigger).pipe(
      tap(() => this.isLoading.set(true)),
      switchMap(() => {
        if (isNullOrUndefined(this.currentUserId)) {
          this.isLoading.set(false);
          return of([]);
        }

        return this.appInstallationService
          .getInstallations({ owner: this.currentUserId })
          .pipe(
            switchMap((installations) => {
              if (installations.length === 0) {
                this.isLoading.set(false);
                return of([]);
              }

              // Filter out installations with invalid appIds and fetch app details
              const validInstallations = installations.filter(
                (inst) =>
                  !isNullOrUndefined(inst.appId) && inst.appId.trim() !== '',
              );

              if (validInstallations.length === 0) {
                this.isLoading.set(false);
                return of([]);
              }

              // Deduplicate by appId to prevent fetching the same app multiple times
              const uniqueAppIds = new Set<string>();
              const deduplicatedInstallations = validInstallations.filter(
                (inst) => {
                  if (uniqueAppIds.has(inst.appId)) {
                    return false;
                  }
                  uniqueAppIds.add(inst.appId);
                  return true;
                },
              );

              const appRequests = deduplicatedInstallations.map(
                (installation) =>
                  this.appStoreService.getAppById(installation.appId, this.currentUserId ?? '').pipe(
                    catchError((err) => {
                      console.warn(
                        `Failed to load app details for ${installation.appId}:`,
                        err,
                      );
                      return of(null);
                    }),
                    tap((app) => {
                      if (app && !isNullOrUndefined(app)) {
                        // Enrich app with installation details
                        (app as InstalledAppWithDetails).installationId =
                          installation.id;
                        (app as InstalledAppWithDetails).installedAt =
                          installation.createdAt;
                        (app as InstalledAppWithDetails).isEnabled =
                          installation.isEnabled;
                      }
                    }),
                  ),
              );

              return forkJoin(appRequests).pipe(
                map((apps) =>
                  apps.filter((app) => app !== null && !isNullOrUndefined(app)),
                ),
                tap(() => this.isLoading.set(false)),
                catchError((err) => {
                  console.error('Error loading app details:', err);
                  this.error.set('Unable to load app details.');
                  this.isLoading.set(false);
                  return of([]);
                }),
              );
            }),
            catchError((err) => {
              console.error('Error loading installations:', err);
              this.error.set('Unable to load your installed apps.');
              this.isLoading.set(false);
              return of([]);
            }),
          );
      }),
    ),
    { initialValue: [] as InstalledAppWithDetails[] },
  );

  readonly filteredApps = computed((): InstalledAppWithDetails[] => {
    return (this.installedApps() ?? []) as InstalledAppWithDetails[];
  });

  readonly hasInstalledApps = computed(() => this.filteredApps().length > 0);

  refresh(): void {
    this.error.set(null);
    this.refreshTrigger.update((n) => n + 1);
  }

  uninstallApp(installationId: string): void {
    if (isNullOrUndefined(this.currentUserId)) return;

    this.appInstallationService
      .uninstallApp(installationId, this.currentUserId)
      .pipe(
        tap(() => this.refresh()),
        catchError((err) => {
          console.error('Error uninstalling app:', err);
          this.error.set('Could not uninstall the app. Please try again.');
          return of(null);
        }),
      )
      .subscribe();
  }
}
